.btn {
  margin-top: 1.1rem;
  padding: 1.2rem 3rem;
  border-radius: 3px;
  border: none;
  font-size: 1rem;
  font-family: "Sriracha", cursive;
  margin-bottom: 3rem;
}

.btn:focus {
  outline: none;
}

.dis {
  color: rgba(0, 0, 0, 0.3);
  background: rgb(202, 202, 202);
}

.active {
  color: rgba(0, 0, 0);
  cursor: pointer;
  box-shadow: 1px 1px 4px black;
  background: cornsilk;
}
