.modal-ctn {
  display: flex;
  width: 100vw;
  height: calc(100vh);
  background: rgba(0, 0, 0, 0.349);
  z-index: 50;
  position: fixed;
  left: 0;
  top: 0;
  justify-content: center;
  align-items: center;
}

.modal {
  display: flex;
  position: fixed;
  z-index: 100;
  width: 30rem;
  height: 60%;
  flex-direction: column;
  background: rgb(255, 247, 247);
  justify-content: center;
  align-items: center;
  border-radius: 7px;
}

.modal h1 {
  color: brown;
}

.btn-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  margin-top: 2rem;
}

.ok {
  background: #f7a400;
  text-shadow: 0.2px 0.3px black;
}

.cancle {
  background: #132553;
}

.modal__btn {
  padding: 10px;
  width: 10rem;
  border: none;
  color: white;
  border-radius: 10px;
  font-family: "Sriracha", cursive;
  font-size: 1rem;
  cursor: pointer;
}

.modal__btn:focus {
  outline: none;
}
