.main-table__element,
.darkBlue-bg {
  color: rgb(255, 255, 255);
  text-shadow: 0.5px 0.4px 0.5px black;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 2px 2px 8px black;
  flex-direction: column;
  justify-content: center;
}

.main-table__element,
.blue-bg {
  color: rgb(255, 255, 255);
  text-shadow: 0.5px 0.4px 0.5px black;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 3px 10px black;
  flex-direction: column;
}
